*{
    margin: 0;
    padding: 0;
}

.container{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.918);
}

.calculator{
    height:445px;
    max-width: 460px;
    border-radius: 4px;
    background-color: grey;
}

.output{
    height: 80px;
}

.keys{
    gap: 5px;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.keys button:first-child{
    color: #ffffff;
    grid-column-end: 3;
    grid-column-start: 1;
    background-color: #561a56;
}

.keys button:first-child:hover{
    background-color: #843284;
    color: #fff;
}

.keys button:nth-child(15){
    color: #ffffff;
    grid-row:4/6;
    grid-column: 4/4;
    background-color: green;
}

.keys button:nth-child(15):hover{
    background-color: rgb(34, 150, 34);
    color: #fff;
}

.keys button:nth-child(16){
    grid-column-start: 1;
    grid-column-end: 3;
}

.input,
.result{
    color: #fff;
    height: 40px;
    display: block;
    font-size: 40px;
    text-align: right;
    padding-right: 10px;
}

button{
    border:none;
    font-size: 28px;
    min-width: 80px;
    min-height: 65px;
    border-radius: 4px;
}


button:hover{
    background-color: #ddddddc2;
}